export default {
	data() {
		return {
			addressData: {
				addressId: '',
				mallId: '',
				userId: '',
				userName: '',
				phone: '',
				//地址
				addressName: '在地图选择',
				//详细地址
				detailInfo: '',
				//门牌号
				houseNumber: '',
				// whetherDefault: false,
				whetherDefault: '',
				latitude: '',
				longitude: '',
			}
		}
	},
	onLoad(option) {
		const item = JSON.parse(decodeURIComponent(option.item));
		let title = '';
		if (item.type === 'add') {
			title = '新增收货地址';
			this.addressData.whetherDefault = false;
			console.log("新增");
		}
		if (item.type === 'update') {
			title = '编辑收货地址';
			this.addressData = item
			console.log("编辑");
		}
		console.log("operation{}", item.type);
		this.operation = item.type;
		uni.setNavigationBarTitle({
			title
		})
	},
	methods: {
		switchChange(e) {
			this.addressData.whetherDefault = !(this.addressData.whetherDefault);
		},
		//地图选择地址
		chooseLocation() {
			let that = this;
			uni.authorize({
				scope: 'scope.userLocation',
				success() {
					uni.chooseLocation({
						success: (data) => {
							//地址
							that.addressData.addressName = data.name;
							//详细地址
							that.addressData.detailInfo = data.address;
							that.addressData.latitude = data.latitude;
							that.addressData.longitude = data.longitude;
						}
					});
				},
				fail(){
					that.$msg('请在设置中开启权限');
				}
			})
		},
		//提交
		confirm() {
			console.log("提交的数据", this.addressData);
			this.addressData.mallId = this.$base_api.MALL_ID;
			// this.addressData.userId = this.$cache.get(this.global.USER_INFO).openId;
			let data = this.addressData;
			if (!data.userName) {
				this.$msg('请填写收货人姓名');
				return;
			}
			if (!/^[1]([3-9])[0-9]{9}$/.test(data.phone)) {
				this.$msg('请输入正确的手机号码');
				return;
			}
			if (!data.detailInfo) {
				this.$msg('请在地图选择所在位置');
				return;
			}
			if (!data.houseNumber) {
				this.$msg('请填写门牌号信息');
				return;
			}
			//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
			//保存地址信息
			let that = this;
			let url = this.$base_api.saveAddress + "/" + this.operation;
			console.log("路径", data);
			this.$minApi.uniapp_post(url, JSON.stringify(data), 'json').then(res => {
				if (res.success) {
					console.log("保存信息成功");
					this.$msg(`地址${this.operation=='update' ? '修改': '添加'}成功`);
					let pages = getCurrentPages(); // 当前页面
					let beforePage = pages[pages.length - 2]; // 前一个页面
					uni.navigateBack({
						success: function() {
							beforePage.onLoad(); // 执行前一个页面的onLoad方法
						}
					});
					// that.$prePage().refreshList(data, that.operation);
				} else {
					this.$msg('保存信息失败{}', res.msg);
				}
			});
			// setTimeout(() => {
			// 	uni.navigateBack()
			// }, 800)
		},
	}
}
